* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {

}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.primary_text_input {
  padding: 2px 4px;
  margin-bottom: 12px;
  padding: 6px 6px;
  width: 260px; }

.primary_button {
  border: none;
  background-color: #33cc33;
  color: white;
  display: block;
  font-size: 14px;
  padding: 6px 12px; }

.user_detail_container {
  display: flex;
  flex-direction: column;
  margin-bottom: 24px; }

.user_detail_item {
  font-size: 22px;
  text-align: left;
  margin-right: 12px; }

.user_search_result {
  text-align: left;
  display: block; }

ul {
  list-style: none;
  margin-left: 0;
  padding-left: 0; }

li {
  list-style: none;
  min-height: 40px;
  line-height: 40px;
  padding-left: 12px;
  border-top: 1px #CCC solid;
  text-align: left; }

li:last-of-type {
  border-bottom: none; }

.fixed_content {
  position: fixed; }

.search_form.error {
  color: red;
  margin-left: 6px;
  font-size: 12px; }

.SearchRepos_error__2l7_1 {
  color: red; }

.SearchRepos_columns_2__1uMOF {
  width: 50%; }

.SearchRepos_search_container__2qutc {
  display: flex;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto; }

